import React from 'react'
import {
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    Divider
} from '@material-ui/core'
import HomeIcon from '@material-ui/icons/Home';
import PregnantWomanIcon from '@material-ui/icons/PregnantWoman';
import ChildFriendlyIcon from '@material-ui/icons/ChildFriendly';
import FaceIcon from '@material-ui/icons/Face';
import DirectionsRunIcon from '@material-ui/icons/DirectionsRun';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';
import LocalHospitalIcon from '@material-ui/icons/LocalHospital';
import AnnouncementIcon from '@material-ui/icons/Announcement';
import LanguageIcon from '@material-ui/icons/Language';
import BugReportIcon from '@material-ui/icons/BugReport';

const Listas = () => {
    return (
        <div>
            <List component='nav'>
                <ListItem button>
                    <ListItemIcon>
                        <HomeIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Inicio
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <PregnantWomanIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Gestacion
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <ChildFriendlyIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        0 a 2 años
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <FaceIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        2 a 4 años
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <DirectionsRunIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        4 años y mas
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <BusinessCenterIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Materiales
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <LocalHospitalIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Lugares de atencion
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <AnnouncementIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Preguntas frecuentes
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <LanguageIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Viva la diferencia
                    </ListItemText>
                </ListItem>

                <ListItem button>
                    <ListItemIcon>
                        <BugReportIcon/>
                    </ListItemIcon>
                    <ListItemText>
                        Covid 19
                    </ListItemText>
                </ListItem>

                <Divider/>

            </List>
        </div>
    )
}

export default Listas